body {
    margin: 0;
  }
    
  .slide-enter,
  .slide-exit {
    transition: transform 1000ms ease-out;
  }
  
  .slide-enter {
    transform: translateX(100%);
  }
  
  .slide-enter.slide-enter-active {
    transform: translateX(0%);
  }
  
  .slide-exit {
    position: absolute;
    top: 0;
    width: 100%;
    transform: translateX(0%);
  }
  
  .slide-exit-active {
    transform: translateX(-100%);
  }
  